import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import CIcon from '@coreui/icons-react'
import { cilFindInPage, cilPencil } from '@coreui/icons'
import {
  CFormInput,
  CRow,
  CCol,
  CButton,
  CTooltip,
  CModalHeader,
  CModalTitle,
  CModalBody,
  CModal,
  CImage,
} from '@coreui/react'
import { quotationLists } from '../../mocks/quotationData'
import demo from './../../assets/images/demo.jpg'

const QuotationList = () => {
  const [open, setOpen] = useState(false)
  return (
    <>
      <CRow className="form-box">
        <CCol md={2}>
          <CFormInput label="單號" placeholder="請輸入單號" />
        </CCol>
        <CCol md={2}>
          <CFormInput label="客戶名稱" placeholder="請輸入客戶名稱" />
        </CCol>
        <CCol md={2}>
          <CFormInput type="date" label="日期" placeholder="請輸入客戶名稱" />
        </CCol>
        <CCol md={2} className="d-flex align-items-end">
          <CButton color="primary">搜尋</CButton>
        </CCol>
      </CRow>
      <CRow className="list-head">
        <CCol md={2}>單號</CCol>
        <CCol md={5}>客戶名稱</CCol>
        <CCol md={3}>建立時間</CCol>
        <CCol md={1}>操作</CCol>
      </CRow>
      <div className="list-body">
        {quotationLists && quotationLists.length
          ? quotationLists.map((item) => {
              return (
                <CRow className="list-item" key={item.num}>
                  <CCol md={2}>{item.num}</CCol>
                  <CCol md={5}>{item.client}</CCol>
                  <CCol md={3}>{item.createDate}</CCol>
                  <CCol md={2}>
                    <CTooltip content="編輯" placement="top">
                      <Link to="/quotation/edit" className="action-icon">
                        <CIcon className="" icon={cilPencil} />
                      </Link>
                    </CTooltip>
                    <CTooltip content="預覽" placement="top">
                      <CIcon
                        className="action-icon"
                        icon={cilFindInPage}
                        onClick={() => setOpen(true)}
                      />
                    </CTooltip>
                  </CCol>
                </CRow>
              )
            })
          : null}
      </div>
      <CModal visible={open} onClose={() => setOpen(false)}>
        <CModalHeader>
          <CModalTitle>報價單預覽</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <div className="px-2">
            <CImage fluid src={demo} />
          </div>
        </CModalBody>
      </CModal>
    </>
  )
}

export default QuotationList
